import uniPopup from '@/components/uni-popup/uni-popup.vue';
import articleList from '@/components/articleList.vue';
export default {
	data() {
		return {
			statusBarHeight:0,
			id: -1,
			height: 0,
			scrollHeight: 0,
			show: false,
			article: null,
			author: null,
			is_collect: 0,
			is_like: 0,
			attention: 0,
			more: false,
			type: 0,
			list: [],
			comment: {
				pageNum: 1,
				pageSize: 10,
				list: [],
				loading: true,
				more: false
			},
			reply: {
				comment: {},
				pageNum: 1,
				pageSize: 10,
				list: [],
				loading: true,
				more: false
			},
			showInput: false,
			showInput1: false,
			content: '',
			content1: ''
		};
	},
	components: { articleList, uniPopup },
	onLoad(opt) {
		uni.hideKeyboard()
		if (opt.id) {
			this.id = opt.id;
			this.$Fetch({
				url: 'news/getDataById',
				data: { id: opt.id },
				success: res => {
					this.article = res;
					this.is_collect = res.is_collect;
					this.is_like = res.is_like;
					this.author = res.adminUser;
					this.attention = res.attention;
					this.type = res.type;
					this.getRecommend();
				}
			});
			console.log('文章ID=', opt.id);
			this.getCommentList();
		}
	},
	onReady() {
		let _this = this;
		uni.getSystemInfo({
			success: function(res) {
				// this.height=750/(res.windowWidth)*res.windowHeight
				_this.height = res.windowHeight / 2 + 'px';
				// 计算组件的高度
				let head = uni.upx2px(421.875);
				let foot = uni.upx2px(100);
				_this.scrollHeight = res.windowHeight - foot - head - res.statusBarHeight + 'px';
				_this.statusBarHeight=res.statusBarHeight
			}
		});
	},
	methods: {
		videoErrorCallback() {},
		back() {
			uni.navigateBack({});
		},
		//评论
		send() {
			uni.hideKeyboard();
			this.$Fetch({
				url: 'Comment/add',
				data: {
					comment_type: 0,
					id_value: this.id,
					content: this.content
				},
				success: res => {
					console.log(res);
				}
			});
		},
		send1() {
			uni.hideKeyboard();
			this.$Fetch({
				url: 'Comment/addComment',
				data: {
					parent_id: 0,
					to_user_id: this.id,
					content: this.content1
				},
				success: res => {
					console.log(res);
				}
			});
		},
		getRecommend() {
			this.$Fetch({
				url: 'news/get_recommend',
				data: { type: this.type },
				success: res => {
					this.list = res;
				}
			});
		},
		//关注
		doAttention(id) {
			this.$Fetch({
				url: 'Attention/add',
				data: { id: id },
				success: res => {
					this.attention = res;
					let str = res === 1 ? '已关注' : '已取消关注';
					uni.showToast({
						title: str,
						icon: 'none'
					});
				}
			});
		},
		//收藏
		doCollect(type) {
			this.$Fetch({
				url: 'Collect/add',
				data: {
					collect_type: type,
					id_value: this.id
				},
				success: res => {
					this.is_collect = res;
					let str = res === 1 ? '已收藏' : '已取消收藏';
					uni.showToast({
						title: str,
						icon: 'none'
					});
				}
			});
		},
		//文章点赞
		doZan(type) {
			this.$Fetch({
				url: 'user_like/add',
				data: {
					like_type: type,
					id_value: this.id
				},
				success: res => {
					this.is_like = res;
					let str = res === 1 ? '已点赞' : '已取消点赞';
					uni.showToast({
						title: str,
						icon: 'none'
					});
				}
			});
		},
		//获取评论列表
		getCommentList() {
			let data = this.comment;
			if (data.loading) {
				data.loading = false;
				this.comment = data;
				this.$Fetch({
					url: 'Comment/getData',
					data: {
						comment_type: 0,
						id_value: this.id,
						pageNum: data.pageNum,
						pageSize: data.pageSize
					},
					success: res => {
						console.log(res);
						let arr = res.lists;
						if (arr.length > 0) {
							data.list = data.list.concat(arr);
							if (arr.length === data.pageSize) {
								data.pageNum++;
								data.loading = true;
								data.more = true;
							} else {
								data.more = false;
							}
						}
						this.comment = data;
					}
				});
			}
		},
		//评论点赞
		addCommentLike(item, index) {
			this.$Fetch({
				url: 'Comment/addCommentLike',
				data: { id: item.id },
				success: res => {
					item.is_like = res;
					if (res === 1) {
						item.like_num++;
					} else {
						item.like_num--;
					}
				}
			});
		},
		closePopup() {
			this.show = false;
			this.reply = {
				comment: {},
				pageNum: 1,
				pageSize: 10,
				list: [],
				loading: true,
				more: false
			};
		},
		showPopup(item) {
			//














			this.getReply(item);
			this.show = true;
		},
		getReply(item) {
			let data = this.reply;
			data.comment = item;
			this.reply = data;
			if (data.loading) {
				data.loading = false;
				this.reply = data;
				this.$Fetch({
					url: 'Comment/getComment',
					data: {
						id: item.id,
						pageNum: data.pageNum,
						pageSize: data.pageSize
					},
					debug: true,
					success: res => {
						let len = res.length;
						if (len > 0) {
							data.list = data.list.concat(res);
							if (len === data.pageSize) {
								data.pageNum++;
								data.loading = true;
								data.more = true;
							} else {
								data.more = false;
							}
						}
						this.reply = data;
						this.show = true;
					}
				});
			}
		}
	}
};
